/**
 * @fileoverview This file defines the Payslip page for the employee dashboard.
 * It allows an employee to view their detailed payslip for a selected period,
 * including earnings, deductions, and net pay.
 */
'use client';

import * as React from 'react';
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
  CardFooter,
} from "@/components/ui/card";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Button } from "@/components/ui/button";
import { Separator } from "@/components/ui/separator";
import { FileDown, Building2 } from "lucide-react";

// In a real application, this data would be fetched from a database
// based on the selected period and the logged-in employee.
const mockPayslipDatabase = {};

/**
 * Renders the employee's payslip page.
 * Allows viewing and downloading payslips for different periods.
 * @returns {JSX.Element} The payslip page component.
 */
export default function PayslipPage() {
  const [selectedPeriod, setSelectedPeriod] = React.useState('');
  
  const payslipData = mockPayslipDatabase[selectedPeriod];

  // Calculate totals from payslip data if it exists.
  const totalEarnings = payslipData?.earnings.reduce((sum, item) => sum + item.amount, 0) ?? 0;
  const totalDeductions = payslipData?.deductions.reduce((sum, item) => sum + item.amount, 0) ?? 0;
  const netPay = totalEarnings - totalDeductions;

  const payPeriods = Object.values(mockPayslipDatabase);

  return (
    <div className="space-y-6 max-w-4xl mx-auto">
      {/* Page Header and Period Selector */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-3xl font-bold font-headline">My Payslip</h1>
          <p className="text-muted-foreground">View and download your payslips.</p>
        </div>
        <div className="flex gap-2 w-full sm:w-auto">
          {/* A dropdown to select the pay period */}
          <Select value={selectedPeriod} onValueChange={setSelectedPeriod} disabled={payPeriods.length === 0}>
            <SelectTrigger className="w-full sm:w-[200px]">
              <SelectValue placeholder="Select Period" />
            </SelectTrigger>
            <SelectContent>
              {payPeriods.length > 0 ? (
                payPeriods.map(p => (
                  <SelectItem key={p.id} value={p.id}>{p.period}</SelectItem>
                ))
              ) : (
                <SelectItem value="none" disabled>No periods available</SelectItem>
              )}
            </SelectContent>
          </Select>
          <Button disabled={!payslipData}>
            <FileDown />
            PDF
          </Button>
        </div>
      </div>

      {/* Payslip Details Card */}
      <Card className="overflow-hidden">
        <CardHeader className="bg-muted/30 p-6">
          <div className="flex justify-between items-center">
            <div>
                <div className="flex items-center gap-2">
                    <Building2 />
                    <h2 className="text-2xl font-bold font-headline">ChumTing</h2>
                </div>
                <p className="text-muted-foreground">{payslipData ? `Payslip for ${payslipData.period}` : 'Select a period to view payslip'}</p>
            </div>
            {payslipData && (
              <div className="text-right">
                  <p className="text-sm text-muted-foreground">Pay Date</p>
                  <p className="font-semibold">{payslipData.payDate}</p>
              </div>
            )}
          </div>
        </CardHeader>
        {payslipData ? (
          <>
            <CardContent className="p-6">
                <div className="grid md:grid-cols-2 gap-8">
                    {/* Earnings Section: Lists all income sources for the period. */}
                    <div>
                        <h3 className="font-semibold text-lg mb-4 text-green-600">Earnings</h3>
                        <ul className="space-y-2">
                        {payslipData.earnings.map((item) => (
                            <li key={item.name} className="flex justify-between items-center text-sm">
                                <span>{item.name}</span>
                                <span className="font-mono">₱{item.amount.toFixed(2)}</span>
                            </li>
                        ))}
                        </ul>
                        <Separator className="my-4"/>
                        <div className="flex justify-between items-center font-semibold">
                            <span>Gross Earnings</span>
                            <span className="font-mono">₱{totalEarnings.toFixed(2)}</span>
                        </div>
                    </div>
                    {/* Deductions Section: Lists all deductions for the period. */}
                    <div>
                        <h3 className="font-semibold text-lg mb-4 text-red-600">Deductions</h3>
                        <ul className="space-y-2">
                        {payslipData.deductions.map((item) => (
                            <li key={item.name} className="flex justify-between items-center text-sm">
                                <span>{item.name}</span>
                                <span className="font-mono">(₱{item.amount.toFixed(2)})</span>
                            </li>
                        ))}
                        </ul>
                        <Separator className="my-4"/>
                        <div className="flex justify-between items-center font-semibold">
                            <span>Total Deductions</span>
                            <span className="font-mono">(₱{totalDeductions.toFixed(2)})</span>
                        </div>
                    </div>
                </div>
            </CardContent>
            <CardFooter className="bg-muted/30 p-6">
                {/* Net Pay Section: The final take-home pay. */}
                <div className="w-full flex justify-between items-center">
                    <span className="text-xl font-bold font-headline">Net Pay</span>
                    <span className="text-2xl font-bold font-headline text-primary">₱{netPay.toFixed(2)}</span>
                </div>
            </CardFooter>
          </>
        ) : (
          <CardContent className="p-6 text-center text-muted-foreground">
            No payslip data available for the selected period.
          </CardContent>
        )}
      </Card>
    </div>
  );
}
